import {session, url} from "@hboictcloud/api";
import "../../wwwroot/assets/legacy/hboictcloud-config";
import {User} from "../types";
import {dbUserLogin} from "../db/UserDB";

// Creates session upon logging in or registering
export async function createSession(user: User): Promise<boolean> {
    const data = await dbUserLogin(user)
    if (data.length === 1) {
        session.set('user', {...user, userId: data[0].userId})
        return true
    } else {
        return false
    }
}

// Function used to log out and destroy the session
export function destroySession(): void {
    session.remove('user')
    location.reload()
}

// Checks if user has an active session upon entering the app
export function authorizeUser(): void {
    const currSession: User = session.get('user')
    if (currSession === undefined) {
        url.redirect('../login/')
    }
}

