// Function used to validate user input. Takes a list of strings and checks if empty.
export function validateText(fields: string[]) {
    let valid: number = 0
    fields.forEach(field => {
            if (field != undefined && field.length > 3 && field.length < 25) {
                valid++
            }
        })
    return valid == fields.length
}

// Function used to validate numerical user input. Takes a list of strings and checks if they are empty or not numerical.
export function validateNumber(fields: string[]) {
    let valid: number = 0
    fields.forEach(field => {
        const floatValue = parseFloat(field)
        if (!isNaN(floatValue) && isFinite(floatValue) && floatValue > 1) {
            valid++
        }
    })
    return valid == fields.length
}

// Function used to show error. Place an element with the id=actionFeedback in HTML to make use of this. Works best in containers
// Has a default value but could also be given a custom message for context
export function showError(msg?: string): void {
    const errorMsg: string = msg === undefined ? 'Invoer incorrect, probeer het nog eens.' : msg
    const errorElement: HTMLElement = document.getElementById('actionFeedback')!
    errorElement.className = 'action-feedback action-error'
    errorElement.innerHTML = `<p>${errorMsg}</p>`

    // Hide the element after 3 seconds
    setTimeout(() => {
        errorElement.className = '';
        errorElement.innerHTML = ''
    }, 3000);
}


// Used to parse datetime object from database into readable date
export function dateToString(date: string) {
    // eg. const date = 2023-10-04T23:34:24.000Z
    const dateTime = date.split('T')[0] // 2023-10-04
    return dateTime
}

// Used to get current date to put into database
export function currDateString(): string {
    const dateFull = new Date()
    const dateISO = dateFull.toISOString()
    return dateToString(dateISO)
}

// Function used to capitalize first character. Useful for names
export function capFirstChar(string) {
    if (string != undefined) {
        let start = string.toLowerCase().slice(0 - -1)
        let sec = string.charAt(0).toUpperCase()
        return sec + start
    } else {
        return null
    }
}