import { api } from "@hboictcloud/api";
import "../../wwwroot/assets/legacy/hboictcloud-config";
import {User} from "../types";

// Function used to create new user into database
export async function userCreate(newUser: User): Promise<boolean> {
    const query: string = "INSERT INTO user (email, password, username) VALUES (?, ?, ?);"
    try {
        const data: string | Array<any> = await api.queryDatabase(
            query,
            newUser.email, newUser.password, newUser.username
        )
        return true
    }
    catch (reason: string) {
        console.log(reason)
        return false
    }
}

// Function used to verify if requested user exists
export async function dbUserLogin(user: User): Promise<string | Array<User>> {
    const query: string = "SELECT * FROM `user` WHERE `username` = ? AND `password` = ?"
    try {
        const data: string | Array<any> = await api.queryDatabase(
            query,
            user.username, user.password)
        return data
    }
    catch (reason: string) {
        console.log(reason)
        return reason
    }
}

// Function used to search user by username
export async function dbUserSearch(username: string): Promise<string | Array<User>> {
    const query: string = "SELECT * FROM user WHERE user.username = ?"
    try {
        const data: string | Array<User> = await api.queryDatabase(
            query,
            username
        )
        return data
    }
    catch (reason: string) {
        console.log(reason)
        return reason
    }
}

